.quote-wrapper {
  position: relative;
  padding-left: 1em;
  border-left: 0.2em solid #039be5;
  font-family: 'Roboto', serif;
  font-size: 2.4em;
  line-height: 1.5em;
  font-weight: 100;
}

.quote-wrapper:before,
.quote-wrapper:after {
  font-family: Calibri;
  color: #039be5;
  font-size: 34px;
}

.quote-wrapper:before {
  content: '\201e'
}

.quote-wrapper:after {
  content: '\201c';
}

.quote-wrapper cite {
  font-size: 50%;
  text-align: center;
  top: 50%
}

.quote-wrapper cite:before {
  content: ' \2015 '
}